  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.wsy_bs = new BetterScroll(this.$refs.scrollBox, {
        probeType: 3,
        click: true
      });
      this.CourseInfo = JSON.parse(window.localStorage.getItem("CourseInfo")) || this.$route.params;
      this.wsy_Flag = this.CourseInfo.has_buy

      // 上滑禁止
      // this.wsy_bs.on("scroll", pos => {
      //   console.log(pos.y);
      //   if (pos.y > 0) {
      //     pos.y = 0;
      //   }
      // });
    });
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    //   分享
    onClickRight() {
      this.PopupIsShow = true;
    },
    //   返回首页
    onClickLeft() {
      this.$router.back(1);
    },
    // 跳转到提交订单页面
